@use '../utils' as *;

.footer{
    margin: auto auto rem(36) auto;
    text-align: center;
    font-size: rem(12);
    font-weight: 100;
    color: var(--dark-text2);

    a , a:visited {
        color: var(--dark-text1);
    }

    a:active {
        color: var(--brightred);
    }
}